.cell {
    width: 100%;
    height: var(--cell-size);
    border-bottom: 1px solid black;
    border-top: 1px solid black;
}

.black {
  background-image: url(./blackcircle.png);
  background-size: cover;
}

.white {
  background-image: url(./whitecircle.png);
  background-size: cover;
}


